import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import "./styles.scss"

export default function Donate() {
  const [open, setOpen] = React.useState(false);
  const [scroll, setScroll] = React.useState('paper');

  const handleClickOpen = (scrollType) => () => {
    setOpen(true);
    setScroll(scrollType);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const descriptionElementRef = React.useRef(null);
  React.useEffect(() => {
    if (open) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [open]);

  return (
    <div>
      <Button onClick={handleClickOpen('paper')}>Info</Button>
      <Dialog
        open={open}
        onClose={handleClose}
        scroll={scroll}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle id="scroll-dialog-title">Donate</DialogTitle>
        <DialogContent dividers={scroll === 'paper'}>
          <DialogContentText
            id="scroll-dialog-description"
            ref={descriptionElementRef}
            tabIndex={-1}
          >
            Lanne passe, jai t contacter par mon ancienne cole primaire, Institut Saint Andr, me demandant sil tait possible que je fasse don de matriels informatiques, type tablette ou ordinateur portable, pour des lves de primaire nayant pas accs  un ordinateur portable durant la pandmie. Jai donc fourni 5 ordinateurs portables que ma famille et amis nutilisaient plus. Jai d rinitialiser chacun des pcs et mis  jour les OS. Jai pass deux jours complets  reboot les pcs et jai aussi aider certain levs  remettre leur pc en ordre. Ce travail ma permis de me sentir utile dans une priode de pandmie. 
            <br></br>
            <br></br>
            Preuve : malheuresement je n'arrive plus a retrouver le mail et je n'ai plus accs aux autres documents
          </DialogContentText>
          <div className="imgContainer">
            <img src="assets/pc.jpg" alt=""/>
          </div>
          
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}